ows: 5rem auto;\n    margin: 0;\n    padding: 0;\n    min-height: 100vh;\n    width: 100%;\n}\n\naside {\n    grid-area: 1 / 1 / 3 / 2;\n    display: flex;\n    flex-direction: column;\n    gap: 2em;\n    padding: 1.2em;\n    align-items: stretch;\n    max-height: 100%;\n    background-color: #F8F8FF;\n}\n\nul {\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\n\nmenu,\nnav {\n    padding: 0;\n}\n\n.active-menu-item {\n    background-color: rgba(138, 185, 131, 0.6);\n    border-radius: 10px;\n}\n\naside li {\n    cursor: pointer;\n    padding: 0 10%;\n}\n\n.user-info,\naside li {\n    display: flex;\n    align-items: center;\n    gap: 1rem;\n}\n\naside p {\n    font-size: 1.2rem;\n}\n\n#task-add p {\n    font-weight: bold;\n    color: red;\n}\n\n.user-name-display {\n    font-size: 1.4rem;\n    font-weight: 600;\n}\n\n[data-letters]:before {\n    content:attr(data-letters);\n    display:inline-block;\n    font-size:1em;\n    font-weight: 900;\n    width:2.5em;\n    height:2.5em;\n    line-height:2.5em;\n    text-align:center;\n    border-radius:50%;\n    background: #8AB983;\n    vertical-align:middle;\n    margin-right:1em;\n    color:white;\n    }\n\nheader {\n    grid-area: 1 / 2 / 2 / 3;\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    padding: 1.2em 4rem;\n    margin-top: 1rem;\n}\n\n.toggle-view {\n    display: flex;\n    gap: 0.5rem;\n    cursor: pointer;\n}\n\n.toggle-view img {\n    transform: rotate(90deg);\n    width: 16px;\n}\n\n.toggle-view p {\n    margin: 0;\n}\n\n.data-area {\n    padding: 2rem 4rem;\n}\n\n.data-area h2 {\n    font-size: 1.8rem;\n}\n\narticle.cards-view {\n    grid-area: 2 / 2 / 3 / 3;\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));\n    grid-template-rows: max-content;\n    gap: 2em;\n}\n\narticle.list-view {\n    grid-area: 2 / 2 / 3 / 3;\n    display: grid;\n    grid-template-columns: 1fr;\n    grid-template-rows: min-content;\n    align-items: start;\n    gap: 0;\n}\n\n.task {\n    cursor: pointer;\n    gap: 1rem;\n    display: grid;\n}\n\n\narticle .task {\n    border: 1px solid #F8F8FF;\n    border-radius: 10px;\n    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;\n    display: grid;\n    grid-template-columns: 1fr;\n    padding: 1rem;\n}\n\n\narticle.list-view .task {\n    display: grid;\n    border: none;\n    box-shadow: none;\n    border-bottom: 1px solid lightgray;\n    border-radius: 0;\n    padding: 1rem 0;\n}\n\n.task-title {\n    grid-area: 1 / 1 / 2 / 2;\n    margin: 0;\n}\n\np.task-description {\n    margin: 0;\n    font-size: 0.9rem;\n    color: gray;\n}\n\n.task-categories {\n    grid-area: 3 / 1 / 4 / 2;\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    gap: 1rem;\n    flex-flow: row wrap;\n}\n\n\n.task-category {\n    border: 2px solid;\n    border-radius: 6px;\n    border-color: #8AB983;\n    display: flex;\n    gap: 0.5rem;\n    padding: 0.4rem 1.7rem;\n}\n\n.task-category a {\n    opacity: 0;\n    border: none;\n    border-radius: 50px;\n    color: red;\n    font-weight: bold;\n    font-size: 0.8rem;\n    padding: 0 0.25rem;\n}\n\nspan:hover > a {\n    transform: translate(5px , -5px);\n    opacity: 1;\n}\n\n.task-category-add-button {\n    justify-content: flex-end;\n    \n    font-size: 1.5rem;\n    font-weight: 100;\n    color: #ddd;\n    cursor: pointer;\n}\n\n.due-date {\n    grid-area: 4 / 1 / 5 / 2;\n    display: flex;\n    justify-content: start;\n    align-items: center;\n    color: grey;\n    font-size: 0.9rem;\n}\n\narticle.list-view .time-stamp {\n    grid-area: 2 / 1 / 3 / 3;\n    justify-content: start;\n}\n\n.hidden {\n    display: none;\n}\n\n.calendar-container {\n    display: grid;\n    grid-template-columns: repeat(7, 1fr);\n    grid-template-rows: 6rem;\n    grid-auto-rows: 6rem;\n    margin: 2rem;\n    overflow: hidden;\n    max-width: 1000px;\n}\n\n.calendar-container>div {\n    position: relative;\n}\n\n.calendar-container>div::after,\n.calendar-container>div::before {\n    content: \"\";\n    background-color: black;\n    position: absolute;\n}\n\n.calendar-container>div:nth-child(1):after {\n    width: 100vw;\n    height: 1px;\n    top: 0;\n    left: 0;\n}\n\n.calendar-container>div:nth-child(1n) {\n    border-bottom: 1px solid black;\n}\n\n.calendar-container>div::before {\n    width: 1px;\n    height: 100vh;\n    top: 0;\n    left: 0;\n}\n\n.calendar-container>div:nth-child(7n) {\n    border-right: 1px solid black;\n}\n\n.calendar-container>div {\n    display: grid;\n    grid-template-columns: 1fr;\n    grid-auto-rows: min-content;\n    gap: 0.5rem;\n    padding: 0.5rem;\n}\n\n.calendar-container p {\n    margin: 0;\n    justify-self: right;\n    align-self: start;\n    font-size: 1rem;\n}\n\n.inactive {\n    color: lightgray;\n}\n\n.active {\n    color: green;\n    font-weight: bold;\n}\n\n\n.task-calendar {\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n}\n\n.task-calendar .task-description,\n.task-calendar .due-date,\n.task-calendar .task-categories {\n    display: none;\n}\n\n.task-calendar .task-title {\n    margin: 0;\n    font-size: 0.7rem;\n    border-radius: 3px;\n    background-color: #8AB983;\n    padding: 0.1rem 0.3rem;\n}\n\ndialog {\n    border-radius: 10px;\n    outline: none;\n    border: none !important;\n    padding: 0;\n    width: 50%;\n    max-width: 500px;\n}\n\n.close {\n    position: absolute;\n    top: 10px;\n    right: 10px;\n    cursor: pointer;\n}\n\n.edit {\n    position: absolute;\n    top: 10px;\n    right: 45px;\n    cursor: pointer;\n}\n\n.delete {\n    position: absolute;\n    bottom: 10px;\n    right: 10px;\n    cursor: pointer;\n}\n\ndialog::backdrop {\n    backdrop-filter: blur(4px);\n}\n\n\n#task-active-task, #new-task-form {\n    display: grid;\n    gap: 1.5rem;\n    padding: 2rem 2.5rem;\n}\n\n#task-active-task .task-title {\n    font-size: 1.4em;\n    font-weight: 900;\n}\n\n#task-active-task .task-category {\n    pad